<template>
  <button type="button" class="a-btn" 
    :class="{'a-btn-primary':isPrimary,'a-btn-danger':isDanger}"
    @click="click"
  >
      <slot></slot>
  </button>
</template>

<script>
export default {
    name:'AButton',
    props:{
        btnType:{
            type:String,
            default:'default'
        }
    },
    computed:{
        isPrimary:function(){
            if(this.btnType==='primary')
              return true
            return false
        },
        isDanger(){
            if(this.btnType==='danger')
              return true
            return false
        }
    },
    methods:{
        click(ev){
            this.$emit('click',ev)
        }
    }
}
</script>

<style>
.a-btn{
    /* line-height: 1.499; */
    /* position: relative; */
    display: inline-block;
    /* font-weight: 400; */
    white-space: nowrap;
    text-align: center;
    background-image: none;
    border: 1px solid transparent;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    /* -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none; */
    user-select: none;
    /* touch-action: manipulation; */
    height: 32px;
    padding: 0 15px;
    font-size: 14px;
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.65);
    background-color: #fff;
    border-color: #d9d9d9;
}
.a-btn:hover{
    color: #40a9ff;
    /* background-color: #fff; */
    border-color: #40a9ff;
}
.a-btn:active{
    color: #096dd9;
    /* background-color: #fff; */
    border-color: #096dd9;
}

.a-btn.a-btn-primary{
    color: #fff;
    background-color: #1890ff;
    border-color: #1890ff;
}
.a-btn.a-btn-primary:hover{
    /* color: #fff; */
    background-color: #40a9ff;
    border-color: #40a9ff;
}
.a-btn.a-btn-primary:active{
    background-color: #096dd9;
    border-color: #096dd9;
}

.a-btn.a-btn-danger{
    color: #f5222d;
    background-color: #f5f5f5;
    border-color: #d9d9d9;
}
.a-btn.a-btn-danger:hover{
    color: #fff;
    background-color: #ff4d4f;
    border-color: #ff4d4f;
}
.a-btn.a-btn-danger:active{
    color: #fff;
    background-color: #cf1322;
    border-color: #cf1322;
}
</style>