<template>
  <div id="sketchpad">
    <svg
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 500 400"
    >
      <polyline id="line1" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line2" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line3" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line4" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line5" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line6" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line7" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line8" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line9" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <polyline id="line10" points style="stroke:var(--stroke-color);stroke-width:2;fill-opacity:0" />
      <line x1="20" y1="200" x2="490" y2="200" style="stroke:var(--mark-color);stroke-width:2" />
      <circle cx="20" cy="200" r="10" fill="var(--mark-color)" />
    </svg>
  </div>
</template>

<script>
export default {
  name: "Sketchpad",
  data(){
      return {
          isDrawing:false,
          baseSet:false,
          baseX:0,
          baseY:0,
          i:1,
          polyline:undefined,
          waveform:{
            x:[],
            y:[]
          }
      }
  },
  methods:{
    redoDraw(){
      document.getElementsByTagName("polyline").forEach(el=>{
        el.setAttribute("points","")
      })
      this.waveform.x=[]
      this.waveform.y=[]
      this.i=1
    }
  },
  mounted(){
      document.getElementById("sketchpad").addEventListener("mousedown",(ev)=>{
            if(!this.baseSet){
                this.baseX=ev.clientX-10
                this.baseY=ev.clientY-200
                this.baseSet=true
            }   
            this.polyline=document.getElementById("line"+this.i)  
            this.polyline.setAttribute('points', this.polyline.getAttribute('points') + " " + (ev.clientX-this.baseX) + "," + (ev.clientY-this.baseY))
            this.i++     
            this.isDrawing=true
            this.waveform.x.push(ev.clientX)
            this.waveform.y.push(ev.clientY)
        })
        document.getElementById("sketchpad").addEventListener("mousemove",(ev)=>{
            if(this.isDrawing){
                this.polyline.setAttribute('points', this.polyline.getAttribute('points') + " " + (ev.clientX - this.baseX) + "," + (ev.clientY - this.baseY))
                this.waveform.x.push(ev.clientX)
                this.waveform.y.push(ev.clientY)
            }
        })
        document.addEventListener("mouseup",(ev)=>{
            this.isDrawing=false
        })
  }
};
</script>

<style scoped>
#sketchpad {
  height: 400px;
  width: 500px;
  background-color: black;
  --mark-color:#666;
  --stroke-color:#fff;
}
</style>