<template>
  <div class="happysynth-draw">
    <div class="happysynth-draw-btns-container">
      <a-button @click="$refs.postConfirm.show()" btn-type='primary'><icon icon-type='check'></icon></a-button>      
      <a-button @click="$refs.redoConfirm.show()" btn-type='danger'><icon icon-type='redo'></icon></a-button>
    </div>
    <div>
      <sketchpad ref="sketchpad"></sketchpad>
    </div>
    <modal ref="redoConfirm" title="是否重画？" modal-type='slim' @clickOK="redo()">
      <p>点击确认将擦除所有笔迹</p>
    </modal>
    <modal ref="postConfirm" title="是否提交波形？" @clickOK="postWaveform()">
      <p>点击确认向服务器提交您绘制的波形</p>
    </modal>
    <message-container>
      <message :is-shown='postMsg'><icon icon-type="loading" style="color:blue"></icon> 提交并制作音色中</message>
      <message :is-shown='postFailMsg'><icon icon-type="warning" style="color:red"></icon> 提交失败</message>
      <message :is-shown='postSuccessMsg'><icon icon-type="check" style="color:green"></icon> 完成</message>
    </message-container>
  </div>
</template>

<script>
import Sketchpad from '../../components/happysynth/Sketchpad'
import Icon from '../../components/common/Icon/Icon'
import AButton from '../../components/common/AButton/AButton'
import Modal from '../../components/common/Modal/Modal'
import Message from '../../components/common/Message/Message'
import MessageContainer from '../../components/common/Message/MessageContainer'
import {waveform} from '../../network/happysynth'
export default {
  name:'HappySynthDraw',
  components:{
    Sketchpad,Icon,AButton,Modal,MessageContainer,Message
  },
  data(){
    return {
      postMsg:false,
      postSuccessMsg:false,
      postFailMsg:false
    }
  },
  methods:{
    redo(){
      this.$refs.sketchpad.redoDraw()
      this.$refs.redoConfirm.close()
    },
    postWaveform(){
      this.$refs.postConfirm.close()
      this.postMsg=true     
      waveform.post('',this.$refs.sketchpad.waveform)
      .then(res=>{
        this.postMsg=false
        this.postSuccessMsg=true
        setTimeout(()=>{this.postSuccessMsg=false},500)
      })
      .catch(err=>{
        console.log(err)
        this.postMsg=false
        this.postFailMsg=true
        setTimeout(()=>{this.postFailMsg=false},500)
      })
    }
  }
}
</script>

<style>
.happysynth-draw{
  display: flex;
  align-items: center;
}
.happysynth-draw-btns-container{
  height: 150px;
  margin-right: 20px;
  /* width: 200px; */
  /* background: rgb(0,0,0,0.1); */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
</style>